import { FC, useEffect, useState } from 'react';

import {
  AboutUsStyles,
  AboutUsContainer,
  AboutUsPreviewBlock,
  AboutUsPreviewImg,
  AboutUsContent,
  AboutUsTitle,
  TextContainer,
  AboutUsText,
  Gallery,
  AboutUsDecorationText,
  ImagesScroll,
  AboutUsImageBlock,
  AboutUsImage,
} from './AboutUs.elements';

import { useServerQuery } from '../../hooks';
import { GetPhotosSalon, GetPhotosSalonResponse } from '../../server';

export const AboutUs: FC = () => {
  const [preview, setPreview] = useState<GetPhotosSalonResponse>();

  const { data, isLoading } = useServerQuery(
    'salonPhotos',
    GetPhotosSalon,
    undefined
  );

  useEffect(() => {
    if (data !== undefined) {
      setPreview(data![0]);
    }
  }, [isLoading]);

  const imgAddress = process.env.REACT_APP_IMG_ADDRESS;

  return (
    <AboutUsStyles>
      <AboutUsContainer>
        {data && data?.length > 0 && (
          <AboutUsPreviewBlock>
            {preview !== undefined && (
              <AboutUsPreviewImg
                src={imgAddress + preview?.src}
                alt={preview?.alt}
                onLoad={(event) => {
                  const target = event.target as HTMLTextAreaElement;
                  target.style.display = 'block';
                }}
              />
            )}
          </AboutUsPreviewBlock>
        )}
        <AboutUsContent>
          <AboutUsTitle> </AboutUsTitle>
          <TextContainer>
            <AboutUsText>
               , ,       -  
                  .    
                    .
            </AboutUsText>
            <AboutUsText>
                  ,   
                    Body-Line.
                 , ,
               ,     .
            </AboutUsText>
            <AboutUsText>
               ,  ,  , 
                   .
            </AboutUsText>
            <AboutUsText>
                    
                .  
                    , 
                 .
            </AboutUsText>
            <AboutUsText>
                -  .  -
                  ,    .
                    
                .     
                  .  , 
                    !
            </AboutUsText>
          </TextContainer>
          {data && data?.length > 0 && (
            <Gallery>
              <AboutUsDecorationText length="45px">
                 
              </AboutUsDecorationText>
              <ImagesScroll>
                {data?.map((photo) => {
                  return (
                    <AboutUsImageBlock key={photo.id}>
                      <AboutUsImage
                        src={imgAddress + photo.src}
                        alt={photo.alt}
                        onClick={() => setPreview(photo)}
                        onLoad={(event) => {
                          const target = event.target as HTMLTextAreaElement;
                          target.style.display = 'block';
                        }}
                      />
                    </AboutUsImageBlock>
                  );
                })}
              </ImagesScroll>
            </Gallery>
          )}
        </AboutUsContent>
      </AboutUsContainer>
    </AboutUsStyles>
  );
};
